import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 全局注册组件
import './index'
// 引入element-ui
import ElementUI from 'element-ui'
// 自定义主题颜色
import './assets/css/element-variable.scss'
// 调用element中的install方法注册
Vue.use(ElementUI, {
	size: 'small',
})
// 引用i18n设置国际化
import i18n from './langs'
// 全局注册过滤器filters
import './filters'
// 全局混入mixin
import './mixins'
// 引入全局自定义指令
import './directives'

Vue.config.productionTip = false

new Vue({
  router,
	i18n,
  store,
  render: h => h(App)
}).$mount('#app')
